/**
 * Editor styles for CAES Gallery Block
 */

.caes-gallery-block {

	// Preview mode
	.caes-gallery-preview {
		background-color: #fff;
		border: 2px solid #2271b1;

		.gallery-row {
			display: grid;
			gap: var(--wp--style--block-gap, 1rem);
			margin-bottom: 0;

			&:last-child {
				margin-bottom: 0;
			}

			// Dynamic column classes for preview
			&.gallery-row-1-cols {
				grid-template-columns: repeat(1, 1fr);
			}

			&.gallery-row-2-cols {
				grid-template-columns: repeat(2, 1fr);
			}

			&.gallery-row-3-cols {
				grid-template-columns: repeat(3, 1fr);
			}

			&.gallery-row-4-cols {
				grid-template-columns: repeat(4, 1fr);
			}

			&.gallery-row-5-cols {
				grid-template-columns: repeat(5, 1fr);
			}

			&.gallery-row-6-cols {
				grid-template-columns: repeat(6, 1fr);
			}

			// Cropped images mode in preview - use flexbox like WP core gallery
			&.is-cropped {
				display: flex;
				flex-wrap: wrap;
				gap: var(--wp--style--block-gap, 1rem);

				.gallery-item {
					display: flex;
					flex-grow: 1;
					flex-direction: column;
					justify-content: center;
					position: relative;

					>div {
						flex: 1 0 0%;
						height: 100%;
						width: 100%;
					}

					img {
						flex: 1;
						height: 100%;
						object-fit: cover;
						width: 100%;
					}
				}

				// Set widths for each column count
				&.gallery-row-1-cols .gallery-item {
					width: 100%;
				}

				&.gallery-row-2-cols .gallery-item {
					width: calc(50% - (var(--wp--style--block-gap, 1rem) / 2));
				}

				&.gallery-row-3-cols .gallery-item {
					width: calc(33.333% - (var(--wp--style--block-gap, 1rem) * 2 / 3));
				}

				&.gallery-row-4-cols .gallery-item {
					width: calc(25% - (var(--wp--style--block-gap, 1rem) * 3 / 4));
				}

				&.gallery-row-5-cols .gallery-item {
					width: calc(20% - (var(--wp--style--block-gap, 1rem) * 4 / 5));
				}

				&.gallery-row-6-cols .gallery-item {
					width: calc(16.666% - (var(--wp--style--block-gap, 1rem) * 5 / 6));
				}
			}

			.gallery-item {
				>div {
					display: block;
					overflow: hidden;
				}

				img {
					width: 100%;
					height: auto;
					display: block;
				}
			}
		}

		// Add gap between rows
		.gallery-row+.gallery-row {
			margin-top: var(--wp--style--block-gap, 1rem);
		}
	}

	// Responsive preview adjustments
	@media screen and (max-width: 782px) {
		.caes-gallery-preview .gallery-row {
			&:not(.is-cropped) {
				grid-template-columns: repeat(2, 1fr) !important;
			}

			&.is-cropped .gallery-item {
				width: calc(50% - (var(--wp--style--block-gap, 1rem) / 2)) !important;
			}
		}
	}

	// Shared gallery-row styles for editor mode
	.caes-gallery-editor {
		.gallery-row {
			display: grid;
			gap: var(--wp--style--block-gap, 1rem);
			margin-bottom: 1rem;

			// Dynamic column classes
			&.gallery-row-1-cols {
				grid-template-columns: repeat(1, 1fr);
			}

			&.gallery-row-2-cols {
				grid-template-columns: repeat(2, 1fr);
			}

			&.gallery-row-3-cols {
				grid-template-columns: repeat(3, 1fr);
			}

			&.gallery-row-4-cols {
				grid-template-columns: repeat(4, 1fr);
			}

			&.gallery-row-5-cols {
				grid-template-columns: repeat(5, 1fr);
			}

			&.gallery-row-6-cols {
				grid-template-columns: repeat(6, 1fr);
			}

			// Cropped mode - flexbox
			&.is-cropped {
				display: flex;
				flex-wrap: wrap;
				gap: var(--wp--style--block-gap, 1rem);

				.gallery-item {
					display: flex;
					flex-grow: 1;
					flex-direction: column;
					justify-content: center;
					position: relative;

					>div {
						flex: 1 0 0%;
						height: 100%;
						width: 100%;
					}

					img {
						flex: 1;
						height: 100%;
						object-fit: cover;
						width: 100%;
					}
				}

				&.gallery-row-1-cols .gallery-item {
					width: 100%;
				}

				&.gallery-row-2-cols .gallery-item {
					width: calc(50% - (var(--wp--style--block-gap, 1rem) / 2));
				}

				&.gallery-row-3-cols .gallery-item {
					width: calc(33.333% - (var(--wp--style--block-gap, 1rem) * 2 / 3));
				}

				&.gallery-row-4-cols .gallery-item {
					width: calc(25% - (var(--wp--style--block-gap, 1rem) * 3 / 4));
				}

				&.gallery-row-5-cols .gallery-item {
					width: calc(20% - (var(--wp--style--block-gap, 1rem) * 4 / 5));
				}

				&.gallery-row-6-cols .gallery-item {
					width: calc(16.666% - (var(--wp--style--block-gap, 1rem) * 5 / 6));
				}
			}

			.gallery-item {
				>div {
					display: block;
					overflow: hidden;
				}

				img {
					width: 100%;
					height: auto;
					display: block;
				}
			}
		}

		.caes-gallery-editor {

			// Gallery header
			.gallery-header {
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-bottom: 20px;
				padding-bottom: 12px;
				border-bottom: 2px solid #ddd;

				h3 {
					margin: 0;
					font-size: 18px;
					font-weight: 600;
				}
			}

			// Individual row editor
			.gallery-row-editor {
				border: 1px solid #ddd;
				padding: 16px;
				margin-bottom: 16px;
				background-color: #f9f9f9;

				// Row header with controls
				.row-header {
					display: flex;
					justify-content: space-between;
					align-items: center;
					margin-bottom: 16px;
					padding-bottom: 12px;
					border-bottom: 1px solid #ddd;

					>div:first-child {
						display: flex;
						align-items: center;
						gap: 12px;

						strong {
							font-size: 14px;
						}

						.components-base-control {
							margin-bottom: 0;
						}
					}

					>div:last-child {
						display: flex;
						gap: 8px;
					}
				}

				// Image preview grid
				.row-images-preview {
					display: grid;
					gap: 12px;
					margin-bottom: 16px;

					.image-preview-item {
						position: relative;
						aspect-ratio: 1;
						background-color: #fff;
						border: 1px solid #ddd;
						overflow: hidden;

						img {
							width: 100%;
							height: 100%;
							object-fit: cover;
						}

						button {
							position: absolute;
							top: 4px;
							right: 4px;
							min-width: auto;
							padding: 4px;
							background-color: rgba(255, 255, 255, 0.9);

							&:hover {
								background-color: rgba(255, 255, 255, 1);
							}
						}
					}
				}

				// Image details editor (collapsed by default)
				details {
					margin-top: 16px;

					summary {
						cursor: pointer;
						font-weight: 500;
						margin-bottom: 12px;
						padding: 8px;
						background-color: #fff;
						border: 1px solid #ddd;

						&:hover {
							background-color: #f0f0f0;
						}
					}

					.images-details-editor {
						.image-detail-item {
							display: flex;
							gap: 12px;
							padding: 12px;
							background-color: #fff;
							border: 1px solid #ddd;
							margin-bottom: 8px;

							>div:first-child {
								flex-shrink: 0;

								img {
									width: 60px;
									height: 60px;
									object-fit: cover;
								}
							}

							>div:last-child {
								flex-grow: 1;

								label {
									display: block;
									margin-bottom: 8px;

									strong {
										display: block;
										margin-bottom: 4px;
										font-size: 13px;
									}

									input {
										width: 100%;
										padding: 6px 8px;
										border: 1px solid #ddd;

										&:focus {
											border-color: #2271b1;
											outline: none;
											box-shadow: 0 0 0 1px #2271b1;
										}
									}
								}

								label:last-of-type {
									margin-bottom: 0;
								}
							}
						}
					}
				}
			}
		}
	}
}